var axios = require('axios');
import {
  getHost
} from "@/service/get-host"
let url = getHost()

export function getAll() {
  return new Promise((res, rej) => {
    var config = {
      method: 'get',
      url: url + '/api/v3/user/cart',
      headers: {
        'Content-Type': 'application/json'
      },
    };
    axios(config)
      .then(function (response) {

        res(response.data.data);
      })
      .catch(function (error) {
        rej(error);
      });

  })
}

// get all cart no login
export function getAllNologin(data) {
  return new Promise((res, rej) => {
    var config = {
      method: 'post',
      url: url + '/api/v1/user/cart',
      headers: {
        'Content-Type': 'application/json'
      },
      data:data
    };
    axios(config)
      .then(function (response) {

        res(response.data.data);
      })
      .catch(function (error) {
        rej(error);
      });

  })
}
// add product local into cart
export function addCartIntoLocal(data,token) {
  return new Promise((res, rej) => {
    var config = {
      method: 'post',
      url: url + '/api/v3/user/cart/localStorage',
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content-Type': 'application/json'
      },
      data:data
    };
    axios(config)
      .then(function (response) {

        res(response.data.data);
      })
      .catch(function (error) {
        rej(error);
      });

  })
}

